/* description: my-page top영역 */
.my-page-top {
  padding: 40px 8.333333vw;
}

.my-page-top-container {
  display: flex;
  align-items: center;
}

.my-page-top-profile-box {
  width: 120px;
  height: 120px;
}

.my-page-top-profile {
  width: 120px;
  height: 120px;
  border-radius: 120px;
  
  background-position: 50% 50%;
  background-size: 100% 100%;
}

.my-page-top-profile:hover {
  cursor: pointer;
}

.my-page-top-info-box {
  margin-left: 25px;
}

.my-page-info-nickname-container {
  display: flex;
  margin-bottom: 10px;
}

.my-page-info-nickname {
  margin-right: 10px;
  color: rgba(0, 0, 0, 0.7);
  font-size: 24px;
  font-weight: 500;
  line-height: 100%;
}

.my-page-info-nickname-input {
  margin-right: 10px;
  color: rgba(0, 0, 0, 0.7);
  font-family: 'Pretendard';
  font-size: 24px;
  font-weight: 500;
  line-height: 100%;

  padding: 0;
  border: none;
  background: none;
  outline: none;
}

.my-page-info-nickname-button {
  width: 24px;
  height: 24px;
  border-radius: 24px;
}

.my-page-info-nickname-button:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.05);
}

.my-page-edit-icon {
  width: 24px;
  height: 24px;
  background-image: url(asset/write_icon.png);
  background-position: 50% 50%;
  background-size: 100% 100%;
}

.my-page-info-email {
  color: rgba(0, 0, 0, 0.4);
  font-size: 16px;
  font-weight: 500;
}

/* description: my-page bottom영역 */
.my-page-bottom {
  padding: 40px 8.333333vw;
  background-color: rgba(0, 0, 0, 0.05);
}

.my-page-bottom-text {
  font-size: 24px;
  font-weight: 500;
  line-height: 100%;
  margin-bottom: 20px;
}

.my-page-bottom-text-emphasis {
  font-weight: 700;
}

.my-page-bottom-container {
  display: flex;
  margin-bottom: 40px;
}

.my-page-bottom-board-list {
  flex: 8;
}

.my-page-bottom-board-list-nothing {
  flex: 8;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(0, 0, 0, 0.4);
  font-size: 24px;
  font-weight: 500;
  line-height: 100%;
}

.my-page-bottom-write-box {
  flex: 4;
  margin-left: 24px;
}

.user-page-bottom-button {
  background-color: #fff;
  height: 76px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.user-page-bottom-button:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.08);
}

.user-page-bottom-button-text {
  margin: 0 6px;
  font-size: 18px;
  font-weight: 500;
  line-height: 100%;
}

.user-page-right-arrow-icon {
  width: 24px;
  height: 24px;
  background-image: url(asset/arrow_right.png);
  background-position: 50% 50%;
  background-size: 100% 100%;
}